.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}


html{
  --yellow:#FFDF4E;
  --black: #212121;
}

h1,h2,h3,h4,p{
  padding: 0;
  margin: 0;
  font-weight: 400;
}

ul{
  text-decoration: none;
  list-style: none;
  margin:0;
  padding:0;
}

a{
  text-decoration: none;
  cursor: pointer;
  color: var(--black);
  scroll-behavior: smooth;
}

button{
  padding: 5px;
  border: 1px solid var(--black);
  transition: 0.5s ease all;
  cursor: pointer;
  line-height: 1.15;
}


.App {
  font-family: Helvetica, sans-serif;
  font-size: 16px;
  scroll-behavior: smooth;
  color: var(--black);
}

.wrapper{
  max-width: 1400px;
  margin: 0 auto;
  width: 95vw;
}

h2{
  font-size: 4rem;
  margin-bottom: 3vh;
}


img{
  height: 100px;
}


.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modalInner {
  position:fixed;
  background: white;
  transition: all 0.5s;
  width: 80%;
  height: auto;
  top:150%;
  left:50%;
  transform: translate(-50%,-50%);
}

.modal.display .modalInner{
  top: 50%;
}

.display {
  top: 0;
  opacity: 1;
}

.hidden {
  top: 150%;
  opacity: 0;
}

.showR{
  display: block;
}

.hideR{
  display: none;
}